"use client";

import { useEventBuilderStore } from "@/store/eventBuilderStore";
import { useToast } from "@/hooks/use-toast";
import WizardLayout from "./WizardLayout";
import Step1BasicInfo from "./Step1BasicInfo";
import Step2SectionsBuilder from "./Step2SectionsBuilder";
import Step3SectionsOrder from "./Step3SectionsOrder";
import Step4EventSettings from "./Step4EventSettings";
import Step5Preview from "./Step5Preview";

interface EventBuilderWizardProps {
  mode?: "create" | "edit";
  eventId?: string;
  onComplete: () => Promise<void>;
}

export default function EventBuilderWizard({
  mode = "create",
  eventId,
  onComplete,
}: EventBuilderWizardProps) {
  const { toast } = useToast();
  const {
    currentStep,
    title,
    nextStep,
    previousStep,
    areAllSectionsComplete,
    getIncompleteSectionsInfo,
  } = useEventBuilderStore();

  const handleNext = () => {
    // Validate sections completion for step 2
    if (currentStep === 2) {
      if (!areAllSectionsComplete()) {
        const info = getIncompleteSectionsInfo();
        toast({
          title: "ŸÑÿß ŸäŸÖŸÉŸÜ ÿßŸÑŸÖÿ™ÿßÿ®ÿπÿ©",
          description: `Ÿäÿ¨ÿ® ÿ•ŸÉŸÖÿßŸÑ ÿ¨ŸÖŸäÿπ ÿßŸÑÿ£ŸÇÿ≥ÿßŸÖ ŸÇÿ®ŸÑ ÿßŸÑŸÖÿ™ÿßÿ®ÿπÿ©. ÿ™ŸÖ ÿ•ŸÉŸÖÿßŸÑ ${info.completed} ŸÖŸÜ ${info.total} ÿ£ŸÇÿ≥ÿßŸÖ.`,
          variant: "destructive",
        });
        return;
      }
    }

    nextStep();
  };

  const handlePrevious = () => {
    previousStep();
  };

  const handleSaveDraft = async () => {
    try {
      await onComplete();
      toast({
        title: "ÿ™ŸÖ ÿßŸÑÿ≠ŸÅÿ∏",
        description: mode === "edit" ? "ÿ™ŸÖ ÿ≠ŸÅÿ∏ ÿßŸÑÿ™ÿπÿØŸäŸÑÿßÿ™ ÿ®ŸÜÿ¨ÿßÿ≠" : "ÿ™ŸÖ ÿ≠ŸÅÿ∏ ÿßŸÑÿ≠ÿØÿ´ ŸÉŸÖÿ≥ŸàÿØÿ© ÿ®ŸÜÿ¨ÿßÿ≠",
      });
    } catch (error) {
      toast({
        title: "ÿÆÿ∑ÿ£",
        description: "ŸÅÿ¥ŸÑ ÿ≠ŸÅÿ∏ ÿßŸÑÿ™ÿπÿØŸäŸÑÿßÿ™",
        variant: "destructive",
      });
    }
  };

  const handleFinalSubmit = async () => {
    try {
      await onComplete();
      toast({
        title: "ŸÜÿ¨ÿ≠!",
        description: mode === "edit" ? "ÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑÿ≠ÿØÿ´ ÿ®ŸÜÿ¨ÿßÿ≠" : "ÿ™ŸÖ ÿ•ŸÜÿ¥ÿßÿ° ÿßŸÑÿ≠ÿØÿ´ ÿ®ŸÜÿ¨ÿßÿ≠",
      });
    } catch (error) {
      toast({
        title: "ÿÆÿ∑ÿ£",
        description: mode === "edit" ? "ŸÅÿ¥ŸÑ ÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑÿ≠ÿØÿ´" : "ŸÅÿ¥ŸÑ ÿ•ŸÜÿ¥ÿßÿ° ÿßŸÑÿ≠ÿØÿ´",
        variant: "destructive",
      });
    }
  };

  // Render current step
  const renderStep = () => {
    switch (currentStep) {
      case 1:
        return <Step1BasicInfo />;
      case 2:
        return <Step2SectionsBuilder />;
      case 3:
        return <Step3SectionsOrder />;
      case 4:
        return <Step4EventSettings />;
      case 5:
        return <Step5Preview />;
      default:
        return <Step1BasicInfo />;
    }
  };

  const isNextDisabled = () => {
    if (currentStep === 1) {
      return !title.trim();
    }
    if (currentStep === 2) {
      return !areAllSectionsComplete();
    }
    return false;
  };

  return (
    <WizardLayout
      currentStep={currentStep}
      onNext={currentStep === 5 ? handleFinalSubmit : handleNext}
      onPrevious={handlePrevious}
      onSaveDraft={handleSaveDraft}
      nextLabel={currentStep === 5 ? (mode === "edit" ? "ÿ≠ŸÅÿ∏ ÿßŸÑÿ™ÿπÿØŸäŸÑÿßÿ™" : "ÿ•ŸÜÿ¥ÿßÿ° ÿßŸÑÿ≠ÿØÿ´") : "ÿßŸÑÿ™ÿßŸÑŸä"}
      isNextDisabled={isNextDisabled()}
      showSaveDraft={currentStep < 5}
    >
      {renderStep()}
    </WizardLayout>
  );
}

